<template>
	<div id="spotSearch">
		<div id="foodSearchTitle">
			<div id="foodSearchTitleParams">"{{this.spot1}}"</div>
			<div id="foodSearchTitleWord">景点搜索结果</div>
			<back id="back"></back>
		</div>
		<spot-list :spotResult="spot2"></spot-list>
	</div>
</template>

<script>
	import back from "../../../../../views/back.vue"
	import about from "../../../../../views/About.vue"
	var AV=about.AV;
	import spotList from "../../spotList.vue"
	export default{
		name : 'spotSearch',
		data(){
			return{
				spot1:'',
				spot2:null
			}
		},
		components:{
			spotList,
			back
		},
		mounted(){
			this.spot1=this.$route.query.spot1;
			var that=this;
			
			var query = new AV.Query('spots');
			query.limit(8);
			query.equalTo('country',that.spot1);
			query.find().then(function (data) {
				that.spot2=JSON.parse(JSON.stringify(data));
			}); 
		}
	}
</script>

<style scoped="scoped">
	#foodSearch{
		width: 100%;
		height: 100%;
	}
	#back{
		position: absolute;
		left: 40%;
		top: 250px;
	}
	#foodSearchTitle{
		margin: 30px 0 20px 14%;
		width: 300px;
		height:60px;
		background-color: #01DFA5;
		display: flex;
		justify-content: center;
		align-items: center;
		/* position: relative; */
	}
	#foodSearchTitleParams{
		width: 40%;
		font-size: 25px;
		color: #DA81F5;
	}
	#foodSearchTitleWord{
		width: 50%;
		color: red;
		font-size: 20px;
	}
</style>
